select[data-v-14eee503] {
  font-size: 18px;
  position: relative;
  cursor: pointer;
  width: 100%;
  border-radius: 0;
  padding: 10px;
  min-height: 48px;
  outline: none !important;
  background: url("../../assets/ic_keyboard_arrow_down_black_24px.svg") no-repeat 98.5% #fff;
  background-size: 20px;
  -webkit-appearance: none;
  border: 1px solid #292929;
}



